<template>
  <div class="cy_win">
     <div class="cy_win_con">
         <div class="line"></div>
        <!--  -->
        <div id="mySlider1" class="mySlider">
          <div class="content">
            <div class="item">
                <img src="../../../assets/img/a1.png">
            </div>
            <div class="item">
                <img src="../../../assets/img/a2.png">
            </div>
            <div class="item">
                <img src="../../../assets/img/a3.png">
            </div>
            <div class="item">
                <img src="../../../assets/img/a4.png">
            </div>
          </div>
        </div>
<div class="codeReadonlyModule" style="height:180px">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
  &lt;script type="text/javascript"&gt;
    import slider from "cy_static/slider";
    slider({
        elem: "#mySlider1",
        content: ".content",
        isShowNum: true
    })
  &lt;/script&gt;
</xmp>
</div>
        <!--  -->
        <div class="line"></div>
        <div id="mySlider2" class="mySlider">
          <div class="content">
            <div class="item">
                <img src="../../../assets/img/t1.png">
            </div>
            <div class="item">
                <img src="../../../assets/img/t2.png">
            </div>
            <div class="item">
                <img src="../../../assets/img/t3.png">
            </div>
            <div class="item">
                <img src="../../../assets/img/t4.png">
            </div>
          </div>
        </div>
<div class="codeReadonlyModule" style="height:190px">
<codemirror ref="myEditor2" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal2" style="display:none">
  &lt;script type="text/javascript"&gt;
    import slider from "cy_static/slider";
    slider({
        elem: "#mySlider", 
        content: ".content",
        isLoop: false,
        autoTime: 5000	
    });
  &lt;/script&gt;
</xmp>
</div>
        <div class="line"></div>
     </div>       
  </div>
</template>

<script>
  import {trim} from "../../../assets/js/codeStyle";
  import sliderScroll from "cy_static/sliderScroll";
  import slider from "cy_static/slider";
  export default {
    name: 'example',
    data () {
      return {
        outWin : null,// 
        cmOptions:{
              readOnly:"true",
              mode : "text/html",  // 
              indentUnit : 2,  // 2
              smartIndent : true,  // 
              tabSize : 4,  // Tab4
              showCursorWhenSelecting : true

        }
      }
    },
    mounted(){
        this.initOutWin();
        this.example1();
        this.example2();

        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });

    },
    methods: {         
        initOutWin : function () {
            var m = this, cy_win = document.querySelector('.cy_win');   
            if (m.outWin) return;             
            m.outWin = sliderScroll({
                elem: cy_win,// 
                content: ".cy_win_con",
                isTopEffect: false,
                isBotEffect: false,
                isLockfull: true,
                pushDown: {}// 
            });
            m.outWin.isEndPull(true);
            m.outWin.isEndPush(true);
        },
        example1 () {
            slider({
				  elem: "#mySlider1",
                  content: ".content",
                  isShowNum: true
            })
        },
        example2 () {
            slider({
				  elem: "#mySlider2",
                  content: ".content",
                  isLoop: false,
                  autoTime: 5000
            })
        }
    },
    activated () {
        this.outWin.setActivated(true);
    },
    deactivated(){
        this.outWin.setActivated(false);
    },
    beforeDestroy() {
        this.outWin.destroy();
    }
  }
</script> 
<style>
  .mySlider .navMenu{position:absolute;left:0;right:0;bottom:10px;padding:0;display:inline-block;text-align:center}
  .mySlider .navMenu b{width:6px;height:3px;display:inline-block;background:#f3ec37;margin:0 2px 0 3px;border-radius:2px}
  .mySlider .navMenu b.curr{border-radius:9px;width:20px;background:#f3ec37}
  #mySlider1 .showNum{position:absolute;right:10px;bottom:10px;padding:0px 8px;color:#d3b669;font-size:12px;letter-spacing:1px;border-radius:12px;background-color:rgba(0,0,0,.5)}
  #mySlider1 .showNum b{font-weight:normal;color:#d3b669;font-size: 16px;}

</style>

  <style scoped>
  .codeModule {margin: 5px 6px;padding: 5px 8px;}
  #mySlider1{font-size: 0;position:relative;overflow:hidden;background-color:#fff;margin:0px 0px 0px 0px;border-bottom: 0.5px solid #eff1f4;}
  #mySlider1 .content{display:table;white-space:nowrap}
  #mySlider1 .content .item{width:375px;height: 100px;display:inline-block;color:#6f6f6f;cursor: e-resize;}
  #mySlider1 .content .item img{display: block;width: 100%;height: 100%;}

  #mySlider2.mySlider{font-size: 0;position:relative;overflow:hidden;background-color:#fff;margin:0px 0px 0px 0px;border-bottom: 0.5px solid #eff1f4;}
  #mySlider2 .content{padding-left: 8px;display:table;white-space:nowrap}
  #mySlider2 .content .item{padding:5px 0;padding-right: 8px;width:280px;height: 100px;display:inline-block;color:#6f6f6f;cursor: e-resize;}
  #mySlider2 .content .item img{display: block;width: 100%;height: 100%;border-radius: 6px;}

  </style>


